body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background-image: linear-gradient(
		45deg,
		hsl(2deg 100% 88%) 0%,
		hsl(7deg 100% 88%) 11%,
		hsl(12deg 100% 88%) 22%,
		hsl(16deg 100% 88%) 33%,
		hsl(20deg 100% 89%) 44%,
		hsl(24deg 100% 89%) 56%,
		hsl(27deg 100% 90%) 67%,
		hsl(31deg 100% 91%) 78%,
		hsl(35deg 100% 92%) 89%,
		hsl(39deg 100% 93%) 100%
	);

	height: 100vh;
	overflow: hidden;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
	font-family: 'SEGA LOGO FONT';
	font-style: normal;
	font-weight: normal;
	src: local('SEGA LOGO FONT'), url(/React/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
	font-family: 'SEGA LOGO FONT';
	color: rgba(236, 38, 37, 0.7);
}

h3 {
	font-family: 'SEGA LOGO FONT';
	color: rgba(143, 32, 191, 0.7);
	letter-spacing: 1px;
	word-spacing: 2px;
	margin: 2rem;
}

.searchbox {
	margin: 0 0 20px 0;
	font-style: italic;
	font-family: -apple-system, BlinkMacSystemFont;
	color: #7d37d2;
}

::-webkit-input-placeholder {
	color: #7d37d2;
}

:-ms-input-placeholder {
	color: #7d37d2;
}

::placeholder {
	color: #7d37d2;
}

